@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100&display=swap);
.current-weather-icon{padding:5px 0 0 0}.icon{width:150px;height:auto}

.current-temperature{font-size:100px}

.current-time{margin:40px 0 0 0;font-size:30px}

.current-weather-info{margin-top:50px;display:flex}.current-weather-info div{display:flex;margin:0 70px 0 0;align-self:center;justify-content:flex-start}.current-weather-info img{margin-right:10px}

.slider{width:560px;margin-top:50px;display:inline-block}.slider .item{background-color:#36498c;height:200px;width:95% !important;border-radius:10px;text-align:center;display:flex !important;justify-content:space-around;align-items:center;align-self:center;flex-direction:column}

#input{color:black;font-size:40px;width:100%;margin-top:10px;background:transparent;outline:none;border:2px solid white;color:white;border-radius:50px;padding:5px 40px}

.loader{display:flex;justify-content:center;align-self:center;align-items:center;padding-top:200px}

.city{font-size:50px;padding:5px 0 0 0}

*{margin:0 auto;box-sizing:border-box;font-family:'Inter', sans-serif;color:white;font-weight:100}body{overflow:hidden}.App{background:linear-gradient(#8CA3F6, #000F45);height:100vh}.App .container{max-width:1440px;padding:0 40px;margin:0 auto}

